/* FOOTER
===================================================================== */
.footer {
  background-color: $grey--1;
}

/* CONTACTS
------------------------------------ */
.footer__contacts {
  &--index {
    background-color: $white;
  }
}

.footer__contacts-location {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 27px;
  padding-bottom: 27px;
}

.footer__contacts-appeal {
  @include font();
  @include font-size(16px, 18px);
  margin-right: $gap--s;
}

.footer__contacts-address {
  font-style: normal;
  line-height: 20px;
  color: $grey--5;
}

.footer__contacts-map {
  height: 362px;
  background: $grey__2 url("../img/map/map.jpg") no-repeat center;
  background-size: cover;

  @media only screen and (min-resolution: 2dppx) {
    background-image: url("../img/map/map@2x.jpg");
  }
}

/* OTHER
------------------------------------ */
.footer__bottom {
  padding-top: 39px;
  padding-bottom: 22px;
  text-align: center;
}

.footer__logo {
  display: block;
  margin-bottom: 32px;
}

.footer__logo-icon {
  width: 128px;
  height: 24px;
}

.footer__contacts-address--form-page {
  margin-right: 1px;
}

/* SOCIALS */

.footer__socials-list {
  @include reset-list();
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 21px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
}

.footer__socials-item {
  &:not(:last-child) {
    margin-right: 37px;
  }

  &:first-child {
    margin-left: -8px;
  }
}

.footer__socials-link {
  display: block;
  padding-top: 21px;
  padding-bottom: 19px;
  color: $grey--4;

  &:hover {
    opacity: 1;
    color: $green--1;

    & svg {
      fill: currentColor;
    }
  }

  & svg {
    fill: currentColor;
  }

  &--fb {
    & svg {
      fill: none;
    }

    &:hover svg {
      stroke: currentColor;
      fill: none;
    }
  }

  &:active svg {
    fill-opacity: 0.3;
    stroke-opacity: 0.3;
  }
}

.footer__copyright {
  @include font($font, initial);
  @include font-size(16px, 18px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: $grey--5;

  &:hover svg {
    color: $green--1;
    opacity: 1;
  }
}

/* QUERRIES
------------------------------------ */
@media screen and (min-width: $screen--md) {
  .footer__contacts-location {
    padding: 61px 71px 58px;
  }

  .footer__contacts-appeal {
    @include font-size(20px, 24px);
  }

  .footer__contacts-address {
    @include font-size(16px, 20px);
    text-align: right;

    &--form-page {
      margin-right: 0;
    }
  }

  .footer__contacts-street {
    display: inline-block;
    margin-bottom: 18px;
  }

  .footer__contacts-map {
    height: 400px;
    background-image: url("../img/map/map--md.jpg");

    @media only screen and (min-resolution: 2dppx) {
      background-image: url("../img/map/map@2x--md.jpg");
    }
  }

  .footer__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 38px;
    padding-bottom: 41px;
  }

  .footer__logo {
    margin-bottom: 0;
  }

  .footer__socials-list {
    border: none;
    margin-bottom: 0;
    margin-left: 20px;
  }

  .footer__socials-item {
    &:not(:last-child) {
      margin-right: 28px;
    }

    &:first-child {
      margin-left: 0;
    }
  }

  .footer__socials-icon {
    &--vk {
      width: 30px;
      height: 22px;
    }

    &--inst {
      width: 23px;
      height: 23px;
    }

    &--fb {
      width: 20px;
      height: 27px;
    }
  }

  .footer__copyright-icon {
    margin-left: 22px;
  }
}

@media screen and (min-width: $screen--lg) {
  .footer__contacts {
    position: relative;
  }

  .footer__contacts-location {
    @include center(initial, 0, 0);
    position: absolute;
    top: 95px;
    left: 110px;
    z-index: 5;
    padding: 65px 89px 61px 76px;
    background-color: $white;
  }

  .footer__contacts-appeal {
    margin-right: 97px;
  }

  .footer__contacts-address {
    text-align: left;
  }

  .footer__contacts-street {
    margin-bottom: 16px;
  }

  .footer__contacts-map {
    height: 400px;
    background-image: url("../img/map/map--lg.jpg");

    @media only screen and (min-resolution: 2dppx) {
      background-image: url("../img/map/map@2x--lg.jpg");
    }
  }

  .footer__bottom {
    padding-top: 39px;
    padding-bottom: 43px;
  }
}

@media screen and (min-width: $screen--xl) {
  .footer__contacts-location {
    left: 16%;
  }
}
