.layout-center {
  @include center($screen--s, $gap--s);
}

@media screen and (min-width: $screen--md) {
  .layout-center {
    min-width: $screen--md;
    padding: 0 $gap--md;

    &--form-page {
      padding-left: 70px;
      padding-right: 70px;
    }
  }
}

@media screen and (min-width: $screen--lg) {
  .layout-center {
    min-width: $screen--lg;
    max-width: $screen--xl;
    padding: 0 $gap--lg;
  }
}
