/* FEATURES
------------------------------------ */
.features {
  padding-bottom: 22px;
}

.features__title {
  @include font-size(36px, 40px);
  margin-bottom: 38px;
}

.features__list {
  @include reset-list();
}

.features__item {
  position: relative;
  height: 80px;
  padding-left: 80px;
  color: $grey--5;

  &::before {
    content: "";
    position: absolute;
    top: -1px;
    left: 0;
    width: 60px;
    height: 60px;
    background-repeat: no-repeat;
    background-size: 60px;
  }

  &:nth-child(1)::before {
    background-image: url("../img/icons/leaf.svg");
  }

  &:nth-child(2)::before {
    background-image: url("../img/icons/tub.svg");
  }

  &:nth-child(3)::before {
    background-image: url("../img/icons/cutlery.svg");
  }

  &:nth-child(4)::before {
    background-image: url("../img/icons/alarm.svg");
  }

  &:nth-child(4) br {
    display: none;
  }
}

/* QUERRIES
------------------------------------ */
@media screen and (min-width: $screen--md) {
  .features {
    padding-top: 14px;
    padding-bottom: 47px;
    border-bottom: 1px solid #e6e6e6;
  }

  .features__title {
    @include font-size(60px, 60px);
    margin-bottom: 32px;
  }

  .features__list {
    display: grid;
    grid-template-rows: repeat(2, max-content);
    grid-template-columns: repeat(2, 1fr);
    gap: 37px 59px;
  }

  .features__item {
    height: auto;
    min-height: 280px;
    position: relative;
    margin-bottom: 0;
    padding: 151px 63px 57px 0;

    &::before {
      top: 50px;
      left: 10px;
      box-shadow: 0 0 0 10px $green--1;
    }

    &::after {
      @include font-size(280px, 280px);
      font-family: $font--theme;
      color: $grey--1;
      z-index: -1;
      position: absolute;
      top: 0;
      right: 76px;
    }

    &:nth-child(1)::after {
      content: "1";
      right: 94px;
    }

    &:nth-child(2)::after {
      content: "2";
    }

    &:nth-child(3)::after {
      content: "3";
    }

    &:nth-child(4)::after {
      content: "4";
    }

    &:nth-child(4) br {
      display: initial;
    }
  }
}

@media screen and (min-width: $screen--lg) {
  .features {
    padding-top: 45px;
    padding-bottom: 11px;
    border: none;
  }

  .features__list {
    grid-template-rows: minmax(280px, auto);
    grid-template-columns: repeat(4, 1fr);
    column-gap: 77px;
  }

  .features__item {
    padding-right: 2px;

    &::after {
      right: 0;
    }

    &:nth-child(1)::after {
      right: 16px;
    }
  }
}
