/* PRODUCT
------------------------------------ */

.product {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  padding: 19px $gap--s 25px;
  border-bottom: 1px solid $grey--2;
}

.product__link-img {
  width: 50%;
  align-self: center;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 12px;
  transition: all 0.6s ease;

  &:hover {
    transform: scale(1.1) rotate(10deg);
    opacity: 1;
  }

  &:active {
    opacity: 0.6;
  }
}

.product__img {
  width: 68px;
  height: 78px;
  margin-top: 18px;

  &--1 {
    height: 86px;
    margin-top: 0;
  }

  &--large {
    width: 84px;
    height: 101px;
    margin-top: 10px;
  }
}

.product__desc {
  width: 50%;
  margin-bottom: 19px;
}

.product__name {
  @include font-size(16px, 20px);
  color: $grey--6;
  display: block;
  width: 69%;
  margin-bottom: 14px;
}

.product__table {
  border-collapse: collapse;
  width: 100%;
}

.product__td {
  @include font-size(12px, 15px);
  color: $grey--5;

  &:nth-of-type(even) {
    text-align: right;
  }
}

.product__btn {
  @include font-size(16px, 20px);
  padding-top: 10px;
  padding-bottom: 10px;
}

/* PRODUCT MORE
------------------------------------ */
.product--more {
  text-align: center;
  justify-content: center;
  padding-top: 25px;
}

.product__link-img--more {
  width: 30px;
  height: 30px;
  background: url("../img/icons/plus.svg") no-repeat center;
  margin-bottom: 23px;

  &:hover {
    transform: scale(1.2);
  }
}

.product__desc--more {
  width: 100%;
  margin-bottom: 21px;
}

.product__name--more {
  width: 100%;
  margin-bottom: 8px;
}

.product__tagline {
  @include font-size(12px, 16px);
  color: $grey--5;

  br {
    display: none;
  }
}

.product__btn--more {
  color: $grey--5;
  background-color: $grey--1;

  &:hover,
  &:focus {
    background-color: $grey--2;
    opacity: 1;
  }

  &:active {
    opacity: 1;
    color: rgba($grey--5, 0.3);
  }
}

/* QUERRIES
------------------------------------ */
$indent-top: 111px;

@media screen and (min-width: $screen--md) {
  .product {
    justify-content: center;
    padding: 34px 37px 40px 39px;
    border-bottom: none;
    background-image: linear-gradient($white $indent-top, $grey--1 $indent-top);
  }

  .product__link-img {
    align-items: flex-end;
    width: 168px;
    height: 207px;
    margin-bottom: 29px;
  }

  .product__img {
    width: auto;
    height: auto;
    margin-top: 0;
  }

  .product__desc {
    width: 100%;
    margin-bottom: 34px;
  }

  .product__name {
    @include font-size(20px, 24px);
    width: 60%;
    margin: 0 auto 15px;
    text-align: center;
  }

  .product__tr {
    border-bottom: 1px solid $grey--3;
  }

  .product__td {
    padding-top: 7px;
    padding-bottom: 8px;
    font-size: 16px;
  }

  .product__btn {
    align-self: flex-end;
  }

  /* PRODUCT MORE */
  .product--more {
    position: relative;
    background: none;

    &::after {
      content: "";
      position: absolute;
      top: $indent-top;
      left: 0;
      width: 100%;
      height: calc(100% - 111px); /* Value from $indent-top */
      border: 2px solid $grey--1;
      pointer-events: none;
    }
  }

  .product__link-img--more {
    margin-bottom: 8px;
    background-size: 60px 60px;
    background-position: center 120px;
  }

  .product__name--more {
    width: 60%;
    margin-bottom: 23px;
  }

  .product__tagline {
    @include font-size(16px, 18px);
  }

  .product__tagline br {
    display: initial;
  }
}

@media screen and (min-width: $screen--lg) {
  $indent-top: 87px;

  .product {
    padding-top: 0;
    background-image: linear-gradient($white $indent-top, $grey--1 $indent-top);
  }

  .product__link-img {
    margin-bottom: 42px;
  }

  .product__desc {
    margin-bottom: 32px;
  }

  .product__name {
    width: 90%;
    line-height: 21px;
  }

  .product__td {
    padding-top: 9px;
  }

  /* PRODUCT MORE */
  .product--more {
    background: none;
  }

  .product__link-img--more {
    margin-bottom: 13px;
    background-position: center 124px;
  }

  .product__name--more {
    width: 80%;
  }
}
