@keyframes title {
  from {
    width: 0;
  }

  to {
    width: 100vw;
  }
}

.title {
  @include font($font--theme, initial);
  margin-top: 0;

  &--caps {
    text-transform: uppercase;
  }

  &--deco {
    position: relative;
  }

  &--deco::before,
  &--deco::after {
    content: "";
    position: absolute;
    top: calc(50% + 2px);
    width: 100vw;
    height: 2px;
    background-color: $green--1;
    animation: 2s ease-in title;
  }

  &--deco::before {
    visibility: hidden;
  }

  &--deco::after {
    left: calc(100% + 22px);
  }
}

/* QUERRIES
------------------------------------ */
@media screen and (min-width: $screen--md) {
  .title {
    &--deco::before {
      visibility: visible;
      right: calc(100% + 39px);
    }

    &--deco::after {
      left: calc(100% + 40px);
    }
  }
}
