.catalog-page__main {
  width: 100%;
  padding-top: 27px;
  padding-bottom: 35px;
  color: $black;
}

/* LEAD
------------------------------------ */
.catalog-page__lead {
  margin-bottom: 41px;
}

.catalog-page__lead-title {
  @include font-size(36px, 36px);
}

/* QUERRIES
------------------------------------ */
@media screen and (min-width: $screen--md) {
  .catalog-page__main {
    padding-bottom: 76px;
  }

  .catalog-page__lead {
    margin-bottom: 53px;
  }

  .catalog-page__lead-title {
    @include font-size(60px, 60px);
  }
}

@media screen and (min-width: $screen--lg) {
  .catalog-page__main {
    padding-top: 18px;
    padding-bottom: 86px;
  }

  .catalog-page__lead {
    margin-bottom: 79px;
  }
}
