.form-page__main {
  @include font();
  @include font-size(16px, 18px);
  color: $black;
  width: 100%;
  padding-top: 27px;
  padding-bottom: 32px;
}

/* LEAD
------------------------------------ */
.form-page__lead {
  margin-bottom: 40px;
}

.form-page__lead-title {
  @include font-size(36px, 36px);
  margin-bottom: 37px;
}

.form-page__lead-desc {
  @include font-size(14px, 18px);
}

/* QUERRIES
------------------------------------ */
@media screen and (min-width: $screen--md) {
  .form-page__main {
    padding-bottom: 72px;
  }

  .form-page__lead {
    margin-bottom: 66px;
  }

  .form-page__lead-title {
    @include font-size(60px, 60px);
    margin-bottom: 54px;
  }

  .form-page__lead-desc {
    @include font-size(20px, 30px);
  }
}

@media screen and (min-width: $screen--lg) {
  .form-page__main {
    padding-top: 18px;
    padding-bottom: 82px;
  }
}
