/* FORM
------------------------------------ */

.form__group {
  margin: 0;
  padding: 0;
  border: none;
  color: $grey--5;

  &--about {
    margin-bottom: 12px;
  }

  &--result {
    position: relative;
    margin-bottom: 26px;
    padding-top: 22px;
    padding-bottom: 6px;
    color: $grey--6;
  }

  &--result::before,
  &--result::after {
    content: "";
    position: absolute;
    left: -10%;
    width: 120%;
    height: 1px;
    background-color: #d9d9d9;
  }

  &--result::before {
    top: 0;
  }

  &--result::after {
    bottom: 0;
  }

  &--contacts {
    margin-bottom: 11px;
  }

  &--comment {
    margin-bottom: 10px;
  }

  &--more {
    margin-bottom: 23px;
    color: $black;
  }
}

.form__group-title {
  @include font-size(24px, 26px);
  color: $black;
  margin-bottom: 36px;
  padding: 0;

  &--contacts {
    width: 182px;
  }

  &--contacts::after {
    top: 30%;
  }
}

.form__input-group {
  display: flex;
  align-items: center;
  margin-bottom: 13px;

  &--contacts {
    position: relative;
  }

  &--result {
    display: flex;
    align-items: center;
    margin-bottom: 13px;
  }

  &--more {
    margin-bottom: 12px;
  }
}

.form__input {
  font-family: inherit;
  font-weight: inherit;
  font-style: inherit;
  font-size: inherit;
  line-height: inherit;
  text-transform: inherit;
  color: inherit;
  width: 100%;
  height: 45px;
  border: 1px solid #e7e7e7;
  padding: 13px 16px;
  outline: none;

  &::placeholder {
    color: inherit;
  }

  &:hover {
    border: 1px solid $grey--5;
  }

  &:focus {
    box-shadow: 0 0 0 2px $grey--5;
  }

  &.error,
  &--error {
    box-shadow: 0 0 0 2px $error;
  }

  &:not([type="checkbox"]):not([type="radio"]) {
    flex-grow: 1;
  }

  &--result,
  &--more {
    &:disabled + label,
    &.disabled + label,
    &--disabled + label {
      opacity: 0.3;
      pointer-events: none;
    }

    &:focus + label::before {
      box-shadow: 0 0 0 2px $grey--5;
    }

    &:active + label::before {
      box-shadow: 0 0 0 1px $green--1;
    }
  }

  &--result:checked + label::before {
    background-color: $green--1;
    border: 8px solid $white;
  }

  &--more:checked + label::before {
    background: url("../img/icons/check.svg") no-repeat center;
    background-size: 18px 14px;
  }

  &--contacts {
    padding-right: 42px;

    &:focus + .form__icon-field {
      color: $green--1;
    }

    &.error + .form__icon-field,
    &--error + .form__icon-field {
      color: $error;
    }
  }

  &--message {
    resize: vertical;
    padding: 9px 13px;
    height: 150px;
  }
}

.form__label {
  margin-right: 13px;
  cursor: pointer;
  white-space: nowrap;

  &--required::after {
    content: "*";
  }

  &--contacts {
    margin-right: 8px;
  }

  &--result,
  &--more {
    display: flex;
    align-items: center;
    margin-right: 0;

    &:hover::before {
      box-shadow: 0 0 0 1px #bababa;
    }
  }

  &--result::before,
  &--more::before {
    content: "";
    min-width: 30px;
    height: 30px;
    margin-right: 16px;
    box-shadow: 0 0 0 1px $grey--3;
  }

  &--result::before {
    border-radius: 50%;
  }
}

.form__icon-field {
  position: absolute;
  right: 15px;
  fill: none;
}

.form__submission {
  text-align: center;
}

.form__btn-submit {
  @include font-size(16px, 20px);
  width: 280px;
  margin: 0 auto 21px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.form__required-desc {
  @include font($font, initial);
  @include font-size(14px, 16px);
  display: block;
}

/* QUERRIES
------------------------------------ */
@media screen and (min-width: $screen--md) {
  .form__group {
    &--about {
      margin-bottom: 49px;
    }

    &--result {
      border: 1px solid #e7e7e7;
      margin-bottom: 71px;
      padding: 25px 28px 13px;
    }

    &--result::before,
    &--result::after {
      display: none;
    }

    &--contacts {
      margin-bottom: 49px;
    }

    &--comment {
      margin-bottom: 43px;
    }

    &--more {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: space-between;
      width: 565px;
      margin-bottom: 22px;
    }
  }

  .form__group-title {
    @include font-size(36px, 36px);
    margin-bottom: 76px;

    &--more {
      margin-bottom: 57px;
    }

    &--contacts {
      width: initial;
    }

    &--contacts::after {
      top: calc(50% + 2px);
    }
  }

  .form__input-group {
    @include font-size(20px, 30px);
    margin-bottom: 20px;

    &--result {
      margin-bottom: 13px;
    }

    &--more {
      min-width: 220px;
      margin-bottom: 32px;
    }
  }

  .form__input {
    height: 52px;
    padding: 10px 20px;

    &--result:checked + label::before {
      border-width: 10px;
    }

    &--message {
      height: 174px;
    }
  }

  .form__label {
    margin-right: 17px;

    &--result::before,
    &--more::before {
      min-width: 40px;
      height: 40px;
      margin-right: 20px;
    }
  }

  .form__submission {
    display: flex;
    align-items: center;
  }

  .form__btn-submit {
    @include font-size(20px, 20px);
    min-width: 283px;
    margin: 0;
    margin-right: 61px;
    padding-top: 17px;
    padding-bottom: 15px;
  }

  .form__required-desc {
    @include font-size(16px, 16px);
  }
}

@media screen and (min-width: $screen--lg) {
  .form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
  }

  .form__group {
    &--about {
      width: calc(50% - 40px);
      margin-bottom: 50px;
    }

    &--result {
      width: calc(50% - 40px);
      margin-bottom: 50px;
    }

    &--contacts {
      width: 100%;
      display: flex;
      justify-content: space-between;
      margin-bottom: 66px;
    }

    &--comment {
      width: 100%;
      margin-bottom: 48px;
    }

    &--more {
      width: 100%;
      margin-bottom: 29px;
    }
  }

  .form__input-group {
    &--contacts {
      width: calc(50% - 40px);
    }

    &.form__input-group--contacts {
      margin-bottom: 0;
    }

    &--more {
      min-width: 246px;
    }
  }

  .form__submission {
    width: 100%;
    justify-content: space-between;
  }

  .form__btn-submit {
    width: calc(50% - 40px);
  }

  .form__required-desc {
    width: calc(50% - 40px);
    text-align: left;
  }
}
