/* PRODUCTS
------------------------------------ */
.products__main {
  margin-bottom: 25px;
}

.products__list {
  @include reset-list();
  border-top: 1px solid $grey--2;
  margin-left: -$gap--s;
  margin-right: -$gap--s;
}

/* QUERRIES
------------------------------------ */
@media screen and (min-width: $screen--md) {
  .products__main {
    margin-bottom: 68px;
  }

  .products__list {
    display: grid;
    grid-template-rows: repeat(4, minmax(511px, auto));
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-around;
    gap: 30px 60px;
    border-top: none;
    margin: 0;
  }
}

@media screen and (min-width: $screen--lg) {
  .products__main {
    margin-bottom: 76px;
  }

  .products__list {
    grid-template-rows: repeat(2, minmax(511px, auto));
    grid-template-columns: repeat(4, 1fr);
    gap: 57px 80px;
  }
}
