/* SUB-PRODUCTS
------------------------------------ */
.sub-products__title {
  @include font-size(24px, 24px);
  display: inline-block;
  vertical-align: middle;
  margin-bottom: 36px;

  &::before {
    right: 100%;
  }
}

.sub-products__list {
  @include reset-list();
  margin-bottom: 36px;
}

.sub-products__item {
  @include font-size(14px, 14px);
  color: $grey--5;
  padding-top: 11px;
  padding-bottom: 19px;
  border-bottom: 1px solid $grey--3;

  &:first-child {
    border-top: 1px solid $grey--3;
  }
}

.sub-products__name {
  @include font();
  @include font-size(16px, 16px);
  color: $grey--6;
  margin-top: 0;
  margin-bottom: 13px;
}

.sub-products__details {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}

.sub-products__btn {
  @include font-size(16px, 20px);
  display: block;
  padding-top: 10px;
  padding-bottom: 10px;
}

/* BANNER */
.sub-products__banner {
  @include font-size(16px, 20px);
  width: 100%;
  height: 289px;
  padding: 186px 61px 43px 58px;
  text-align: center;
  color: $white;
  background:
    url("../img/icons/gift.svg") no-repeat center 58px,
    $green--1 url("../img/gift-cat.jpg") no-repeat center;
  background-size: 78px 80px, cover;

  @media only screen and (min-resolution: 2dppx) {
    background:
      url("../img/icons/gift.svg") no-repeat center 58px,
      $green--1 url("../img/gift-cat@2x.jpg") no-repeat center;
    background-size: 78px 80px, cover;
  }
}

/* QUERRIES
------------------------------------ */
@media screen and (min-width: $screen--md) {
  .sub-products__title {
    @include font-size(36px, 36px);
    margin-bottom: 80px;
    padding-left: 37px;
  }

  .sub-products__list {
    margin-bottom: 69px;
  }

  .sub-products__item {
    @include font-size(16px, 20px);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 27px 38px 26px;
    border: 1px solid $grey--3;

    &:not(:last-child) {
      border-bottom: none;
    }
  }

  .sub-products__name,
  .sub-products__details {
    margin-bottom: 25px;
  }

  .sub-products__name {
    @include font-size(20px, 20px);
  }

  .sub-products__amount {
    margin-right: 49px;
  }

  /* BANNER */
  .sub-products__banner {
    @include font-size(16px, 20px);
    display: flex;
    height: 200px;
    padding: 71px 124px 69px 78px;
    text-align: left;
    filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
    background:
      url("../img/icons/gift.svg") no-repeat 78px center,
      $green--1 url("../img/gift-cat--md.jpg") no-repeat center;
    background-size: 80px 82px, cover;

    @media only screen and (min-resolution: 2dppx) {
      background:
        url("../img/icons/gift.svg") no-repeat 78px center,
        $green--1 url("../img/gift-cat@2x--md.jpg") no-repeat center;
      background-size: 80px 82px, cover;
    }
  }

  .sub-products__tagline {
    margin-left: auto;
  }
}

@media screen and (min-width: $screen--lg) {
  .sub-products {
    display: grid;
    grid-template-rows: min-content 1fr;
    grid-template-columns: max-content 1fr max-content;
    column-gap: 79px;
  }

  .sub-products__title {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    padding-left: 0;

    &::before {
      right: calc(100% + 40px);
    }
  }

  .sub-products__list {
    grid-row: 2 / 3;
    grid-column: 1 / 3;
    margin-bottom: 0;
  }

  .sub-products__item {
    padding: 14px 0 17px;
    border-left: none;
    border-right: none;
  }

  .sub-products__name,
  .sub-products__details {
    width: 285px;
    margin-bottom: 0;
  }

  .sub-products__details {
    padding-top: 8px;
  }

  .sub-products__btn {
    width: 246px;
  }

  /* BANNER */
  .sub-products__banner {
    grid-row: 2 / 3;
    grid-column: 3 / 4;
    height: auto;
    padding: 57px 43px 42px 41px;
    justify-content: center;
    text-align: center;
    background:
      url("../img/icons/gift.svg") no-repeat center 56px,
      $green--1 url("../img/gift-cat.jpg") no-repeat center;

    @media only screen and (min-resolution: 2dppx) {
      background:
        url("../img/icons/gift.svg") no-repeat center 56px,
        $green--1 url("../img/gift-cat@2x.jpg") no-repeat center;
      background-size: 80px 82px, cover;
    }
  }

  .sub-products__tagline {
    margin-top: auto;
  }
}
