/* CATEGORIES
------------------------------------ */
.categories {
  margin-bottom: 29px;
}

.categories__item {
  margin-bottom: 17px;
  padding: 21px 33px 0 20px;
  background-color: $grey--1;
}

.categories__lead {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 23px;

  &::before {
    content: "";
    display: block;
  }

  &--1::before {
    width: 50px;
    height: 53px;
    margin-right: 21px;
    background: url("../img/icons/cat--home.svg") no-repeat;
  }

  &--2::before {
    width: 67px;
    height: 50px;
    margin-right: 11px;
    margin-left: -7px;
    background: url("../img/icons/cat--wild.svg") no-repeat;
  }
}

.categories__name {
  @include font-size(24px, 37px);
  margin-bottom: 0;
}

.categories__desc {
  margin-bottom: 23px;
  color: $grey--5;
}

.categories__link {
  display: flex;
  align-items: center;
  padding-top: 18px;
  padding-bottom: 23px;
  border-top: 1px solid #e6e6e6;
  color: $grey--5;

  &:hover {
    opacity: 1;
  }

  &:hover .categories__link-icon {
    width: 32px;
    background: url("../img/icons/arrow--md.svg") no-repeat;
    transition: transform 0.3s linear;
    transform: translateX(15px);
  }

  &:active {
    opacity: 0.3;
  }
}

.categories__link-icon {
  margin-left: 16px;
  width: 24px;
  height: 12px;
  background: url("../img/icons/arrow.svg") no-repeat;
}

/* QUERRIES
------------------------------------ */
@media screen and (min-width: $screen--md) {
  .categories__item {
    margin-bottom: $gap--md;
    padding: 77px 279px 35px 52px;
    position: relative;
    background-repeat: no-repeat;

    &--1 {
      background-image: url("../img/icons/cat--home.svg");
      background-size: 219px 207px;
      background-position: 89% 49%;
    }

    &--2 {
      padding-right: 314px;
      background-image: url("../img/icons/cat--wild.svg");
      background-size: 268px 200px;
      background-position: 93% 52%;
    }
  }

  .categories__lead {
    margin-bottom: 24px;

    &::before {
      display: none;
    }
  }

  .categories__name {
    @include font-size(36px, 36px);
  }

  .categories__desc {
    margin-bottom: 33px;
  }

  .categories__link {
    @include font-size(20px, 30px);
    border: none;
  }

  .categories__link-icon {
    margin-left: 25px;
  }
}

@media screen and (min-width: $screen--lg) {
  .categories {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
  }

  .categories__item {
    margin-bottom: 0;
    padding: 41px 52px 35px;
    background-image: none;
  }

  .categories__lead {
    margin-bottom: 42px;

    &::before {
      display: block;
      margin-right: 62px;
      background-size: cover;
    }

    &--1::before {
      width: 100px;
      height: 106px;
    }

    &--2::before {
      width: 134px;
      height: 100px;
      margin-left: -20px;
      margin-top: 6px;
      margin-right: 47px;
    }
  }

  .categories__desc {
    margin-bottom: 6px;
  }
}
