/* EXAMPLE
------------------------------------ */
.example {
  background-color: #eaeaea;
}

.example__content {
  padding-top: 27px;
  padding-bottom: 35px;
}

.example__desc {
  margin-bottom: 42px;
}

.example__title {
  @include font-size(36px, 40px);
  margin-bottom: 33px;
}

.example__text {
  margin-top: 0;
  margin-bottom: 25px;
  color: $grey--5;
}

.example__list {
  display: flex;
  justify-content: space-between;
  margin-top: 0;
  margin-bottom: 35px;
}

.example__item {
  position: relative;
  width: 124px;
  padding: 12px 12px 15px;
  border: 1px solid $grey__3;
  border-radius: 3px;
  text-align: center;
}

.example__num {
  @include font();
  @include font-size(24px, 24px);
  margin-left: 0;
}

.example__name {
  @include font-size(12px, 12px);
  position: absolute;
  bottom: -16px;
  left: 50%;
  transform: translateX(-50%);
  padding-left: 10px;
  padding-right: 10px;
  background-color: #eaeaea;
  color: $grey--5;
}

.example__finances {
  @include font();
  @include font-size(14px, 20px);
  text-align: center;
}

/* MATCHING Slider */

.example__slider {
  width: 280px;
  margin: 0 auto;
}

.example__slider-images {
  height: 232px;
  overflow: hidden;
  position: relative;
  margin-bottom: 32px;
}

.example__slider-slide {
  z-index: 1;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #eaeaea;
  transition: width 1s;
}

.example__slider-img {
  width: initial;
  max-width: none;
  height: inherit;

  &--1 {
    margin-left: -20px;
  }

  &--2 {
    display: block;
    margin: 0 auto;
  }
}

.example__slider-controls {
  display: flex;
  justify-content: center;
}

.example__slider-btn {
  @include font();
  @include font-size(16px, 20px);
  color: $black;
  border: none;
  cursor: pointer;
  padding: 0;
  border-bottom: 1px dotted rgba($black, 0.24);
  background-color: transparent;

  &:hover,
  &:focus {
    border-bottom: 1px solid rgba($black, 0.4);
  }

  &:active {
    border-bottom-color: transparent;
    outline: none;
    color: $black;
  }
}

.example__slider-range {
  width: 84px;
  margin-left: 18px;
  margin-right: 18px;
  appearance: none;
  background: transparent;

  /* THUMB */
  &::-webkit-slider-thumb {
    appearance: none;
    height: 6px;
    width: 50%;
    border-radius: 6px;
    cursor: pointer;
    background-color: $green--1;
  }

  &::-moz-range-thumb {
    appearance: none;
    height: 6px;
    width: 50%;
    border-radius: 6px;
    cursor: pointer;
    background-color: $green--1;
  }

  /* TRACK */
  &::-webkit-slider-runnable-track {
    padding: 4px 6px;
    border: 1px solid $grey--3;
    border-radius: 6px;
    background-color: $white;
  }

  &::-moz-range-track {
    padding: 4px 6px;
    border: 1px solid $grey--3;
    border-radius: 6px;
    background-color: $white;
  }
}

/* QUERRIES
------------------------------------ */
@media screen and (min-width: $screen--md) {
  .example {
    background: linear-gradient($white 618px, #eaeaea 618px);
  }

  .example__content {
    padding-top: 43px;
    padding-bottom: 47px;
  }

  .example__desc {
    margin-bottom: 59px;
  }

  .example__title {
    @include font-size(60px, 60px);
    margin-bottom: 63px;
  }

  .example__text {
    margin-bottom: 52px;
    padding-right: 7px;
  }

  .example__results {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .example__list {
    margin-bottom: 0;
  }

  .example__item {
    width: 162px;
    margin-right: 42px;
    padding: 15px 15px 20px;
  }

  .example__num {
    @include font-size(30px, 37px);
  }

  .example__name {
    @include font-size(14px, 14px);
    padding-left: 0;
    padding-right: 0;
    width: 82%;
    bottom: -6px;
    background-color: $white;

    &--1 {
      width: 116px;
    }

    &--2 {
      width: 143px;
    }
  }

  .example__finances {
    @include font-size(20px, 20px);
  }

  .example__sum {
    display: block;
    margin-top: 9px;
    text-align: right;
  }

  /* SLIDER */
  .example__slider {
    width: 708px;
    padding: 22px 30px;
  }

  .example__slider-images {
    height: 520px;
    margin-bottom: 15px;
  }

  .example__slider-slide {
    background: linear-gradient($white 197px, #eaeaea 197px);
  }

  .example__slider-img {
    &--1 {
      margin-left: -13px;
    }

    &--2 {
      margin-left: 77px;
    }
  }

  .example__slider-controls {
    max-width: 88%;
    margin: 0 auto;
  }

  .example__slider-btn {
    @include font-size(20px, 30px);
  }

  .example__slider-range {
    flex-grow: 1;
    margin-left: 20px;
    margin-right: 20px;

    &:focus {
      outline: none;
    }

    &::-webkit-slider-thumb {
      width: 34px;
      height: 34px;
      margin-top: -14px;
      border-radius: 50%;
      border: 13px solid $white;
      box-shadow: 0 0 0 1px $grey--3;
      background-color: $green--1;
      filter: drop-shadow(0 1px 3px rgba(0, 0, 0, 0.16));
    }

    &::-webkit-slider-thumb:hover {
      filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.19));
    }

    &::-webkit-slider-thumb:active {
      box-shadow: 0 0 0 1px $green--1;
      filter: drop-shadow(0 3px 5px rgba(0, 0, 0, 0.2));
    }

    &::-moz-range-thumb {
      width: 8px;
      height: 8px;
      margin-top: -14px;
      border-radius: 50%;
      border: 13px solid $white;
      box-shadow: 0 0 0 1px $grey--3;
      background-color: $green--1;
    }

    &::-moz-range-thumb:hover {
      filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.19));
    }

    &::-moz-range-thumb:active {
      box-shadow: 0 0 0 1px $green--1;
      filter: drop-shadow(0 3px 5px rgba(0, 0, 0, 0.2));
    }

    &::-webkit-slider-runnable-track {
      height: 6px;
      padding: 0;
      border: none;
      background-color: #dcdcdc;
    }

    &::-moz-range-track {
      height: 6px;
      padding: 0;
      border: none;
      background-color: #dcdcdc;
    }
  }
}

@media screen and (min-width: $screen--md) and (max-width: 881px) {
  .example__slider-slide {
    background: linear-gradient($white 174px, #eaeaea 174px);
  }
}

@media screen and (min-width: $screen--lg) {
  $example-bg-height: 194px;

  .example {
    background: linear-gradient($white $example-bg-height, $grey--1 $example-bg-height);
  }

  .example__content {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding-top: 0;
    padding-bottom: 75px;
  }

  .example__desc {
    width: 440px;
    margin-bottom: 0;
    padding-top: 59px;
  }

  .example__title {
    margin-bottom: 146px;
  }

  .example__text {
    margin-bottom: 67px;
    padding-right: 5px;
  }

  .example__results {
    flex-wrap: wrap;
  }

  .example__list {
    margin-bottom: 59px;
  }

  .example__item {
    margin-right: 42px;
  }

  .example__name {
    background-color: #eaeaea;
  }

  .example__sum {
    display: initial;
    margin-left: 59px;
  }

  /* SLIDER */
  .example__slider {
    z-index: 1;
    position: absolute;
    right: 110px;
    width: 670px;
    height: 100%;
    padding: 0;

    &::after,
    &.show-bg::after {
      content: "";
      z-index: -1;
      position: absolute;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100%;
      background: linear-gradient($white $example-bg-height, #eaeaea $example-bg-height);
    }

    &.show-bg::after {
      background: linear-gradient($white $example-bg-height, $grey--1 $example-bg-height);
    }
  }

  .example__slider-images {
    height: 100%;
    margin-bottom: 0;
  }

  .example__slider-slide {
    height: 100%;
    padding-bottom: 120px;
    background: linear-gradient($white $example-bg-height, $grey--1 $example-bg-height);
  }

  .example__slider-img {
    height: 520px;

    &--1 {
      margin-left: 20px;
    }

    &--2 {
      margin-left: 111px;
    }
  }

  .example__slider-controls {
    z-index: 5;
    position: absolute;
    top: 554px;
    left: 15%;
    width: 85%;
  }
}
