// VARS
//=====================================================================
// COLORS
$white: #ffffff;
$black: #000000;
$green--1: #68b738;
$green--2: #5eaa2f;
$error: #ff8282;
$grey--1: #f2f2f2;
$grey--2: #ebebeb;
$grey--3: #cdcdcd;
$grey--4: #666666;
$grey--5: #444444;
$grey--6: #222222;

// LAYOUT
$screen--s: 320px;
$screen--md: 768px;
$screen--lg: 1440px;
$screen--xl: 1920px;
$gap--s: 20px;
$gap--md: 30px;
$gap--lg: 110px;

// TEXT
$font: arial, system-ui, -apple-system, sans-serif;
$font--theme: "Oswald", system-ui, -apple-system, "Noto Sans", sans-serif;


// MIXINS
//=====================================================================
@mixin font($ff: $font--theme, $caps: uppercase, $fw: 400) {
  font-family: $ff;
  font-weight: $fw;
  text-transform: $caps;
  font-style: normal;
}

@mixin font-size($fz, $lh) {
  font-size: $fz;
  line-height: $lh;
}

@mixin reset-list() {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin center($min, $gap, $margin: auto) {
  min-width: $min;
  margin: 0 $margin;
  padding: 0 $gap;
}

@mixin center--sm($width) {
  max-width: $width;
  margin: 0 auto;
}
