.btn {
  @include font();
  @include font-size(14px, 18px);
  color: $white;
  text-align: center;
  display: block;
  width: 100%;
  padding: 7px 20px;
  border: none;
  background-color: $green--1;
  cursor: pointer;

  &:hover,
  &:focus {
    opacity: 1;
    background-color: $green--2;
  }

  &:active {
    color: rgba($white, 0.3);
  }
}
