/* PROMO
------------------------------------ */
.promo {
  margin-bottom: 35px;
  background:
    linear-gradient(to bottom, rgba($green--1, 0.85) 302px, white 302px),
    url("../img/promo/cat.jpg") no-repeat top;
  background-size: 100%;

  @media only screen and (min-resolution: 2dppx) {
    background:
      linear-gradient(to bottom, rgba($green--1, 0.85) 302px, white 302px),
      url("../img/promo/cat@2x.jpg") no-repeat top;
    background-size: 100%;
  }
}

.promo__content {
  position: relative;
  padding-top: 27px;
  padding-bottom: 27px;
  text-align: center;
}

.promo__desc {
  @include font();
  line-height: 14px;
  margin-bottom: 29px;
  color: $white;
}

.promo__title {
  @include font-size(36px, 36px);
  margin-top: 0;
  margin-bottom: 25px;
}

.promo__link {
  position: absolute;
  bottom: -12px;
  left: 50%;
  transform: translateX(-50%);
  max-width: $screen--s - 40px;
  padding-top: 11px;
  padding-bottom: 11px;
}

/* QUERRIES
------------------------------------ */
@media screen and (min-width: $screen--md) {
  .promo {
    margin-bottom: 0;
    background: none;
    background-color: $white;
  }

  .promo__content {
    padding-top: 24px;
    padding-bottom: 0;
  }

  .promo__desc {
    padding-left: 87px;
    padding-right: 87px;
    margin-bottom: 20px;
    color: $black;
    text-align: left;
  }

  .promo__title {
    @include font-size(60px, 60px);
    margin-bottom: 41px;
  }

  .promo__slogan {
    @include font-size(20px, 20px);
    margin-bottom: 37px;
  }

  .promo__link {
    @include font-size(20px, 20px);
    position: relative;
    z-index: 1;
    left: 0;
    width: 239px;
    padding: 17px 20px;
    transform: none;
  }

  .promo__img {
    width: 709px;
    height: 606px;
    margin-top: -94px;
  }
}

@media screen and (min-width: $screen--lg) {
  .promo {
    margin-bottom: 82px;
    padding-top: 148px;
    position: relative;
    z-index: 1;

    &::after {
      content: "";
      position: absolute;
      z-index: -1;
      top: 0;
      left: 50%;
      width: 50%;
      height: 100%;
      background: $green--1 url("../img/promo/cat--lg-green.jpg") no-repeat;
      background-size: cover;

      @media only screen and (min-resolution: 2dppx) {
        background: $green--1 url("../img/promo/cat@2x--lg-green.jpg") no-repeat;
        background-size: cover;
      }
    }
  }

  .promo__content {
    display: flex;
    padding-bottom: 46px;
  }

  .promo__desc {
    max-width: 47.6%;
    margin-bottom: 0;
    padding-top: 50px;
    padding-left: 80px;
  }

  .promo__slogan {
    margin-bottom: 40px;
  }

  .promo__img {
    width: 814px;
    height: 500px;
    margin-top: -31px;
    margin-left: -264px;
  }

  .promo__link {
    width: 244px;
  }
}
