/* HEADER
===================================================================== */
.header {
  width: 100%;
  border-bottom: 1px solid #e6e6e6;
  color: $black;
}

.header__top {
  display: flex;
  padding-top: 13px;
  padding-bottom: 14px;
}

.header__logo {
  flex-grow: 1;
  display: flex;
  align-items: center;

  &:active,
  &:focus {
    opacity: 1;
    outline: none;
  }

  &::before {
    content: "";
    width: 33px;
    height: 38px;
    display: inline-block;
    vertical-align: middle;
    background: url("../img/logo--cat.svg") no-repeat center;
  }
}

.header__logo-img {
  margin: 0 auto;
}

.header__toggle-menu {
  width: 24px;
  border: none;
  cursor: pointer;
  background: url("../img/icons/menu.svg") no-repeat center;

  &.active {
    background: url("../img/icons/cross.svg") no-repeat center;
  }

  &.no-js {
    display: none;
  }

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

/* MAIN NAV
------------------------------------ */
.header__main-nav {
  overflow: hidden;
  margin-left: -$gap--s;
  margin-right: -$gap--s;
  transition: height 0.6s ease;
}

.header__main-nav-list {
  @include reset-list();
}

.header__main-nav-link {
  @include font-size(20px, 20px);
  font-weight: 500;
  display: block;
  padding-top: 22px;
  padding-bottom: 24px;
  border-top: 1px solid #e6e6e6;
  text-align: center;

  &:not([href]) {
    color: $green--1;
  }
}

/* QUERRIES
------------------------------------ */
@media screen and (min-width: $screen--md) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 29px;
    margin-bottom: 37px;
    border: none;
  }

  .header__top {
    padding: 0;
    margin-right: 85px;
  }

  .header__logo {
    flex-grow: initial;

    &::before {
      width: 60px;
      height: 50px;
      margin-right: 13px;
      background-image: url("../img/logo--cat-md.svg");
    }
  }

  /* MAIN NAV */

  .header__toggle-menu {
    display: none;
  }

  .header__main-nav {
    height: auto;
    margin-left: initial;
    margin-right: initial;
  }

  .header__main-nav-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
  }

  .header__main-nav-item:not(:last-child) {
    margin-right: 35px;
  }

  .header__main-nav-link {
    @include font-size(18px, 24px);
    padding-top: 9px;
    padding-bottom: 8px;
    border: none;
    font-weight: 400;

    &:not([href]) {
      color: inherit;
      border-bottom: 2px solid $green--1;
    }
  }
}

@media screen and (min-width: $screen--lg) {
  .header {
    margin-bottom: 57px;
    padding-top: 54px;
    color: #111111;

    &--index {
      position: absolute;
      top: 54px;
      left: 0;
      right: 0;
      z-index: 5;
      padding-top: 0;
    }
  }

  .header__logo {
    &::before {
      width: 70px;
      height: 59px;
      background-image: url("../img/logo--cat-lg.svg");
    }
  }

  .header__logo-img {
    width: 117px;
    height: 21px;
  }

  .header__main-nav {
    width: 50%;
    padding-left: 20px;
  }

  .header__main-nav-link {
    @include font-size(20px, 30px);
    color: #111111;
    padding-bottom: 6px;

    &--index {
      color: $white;

      &:not([href]) {
        border-bottom-color: $white;
        color: $white;
      }
    }
  }
}
